/* dashboard.css */
/* ChangePasswordForm.css */

.change-password-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.change-password-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  position: relative;
  max-width: 400px;
  width: 100%;
}

.change-password-container .close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.change-password-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Center-aligns the form items */
}

.change-password-form label {
  color: #387cb8;
  margin: 10px;
}

.change-password-form input {
  display: block;
  width: 85%;
  padding: 0.8rem 0 0.8rem 1.6rem;
  font-size: 0.8em;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 30px;
}

.change-password-form button {
  padding: 0.4rem 1.6rem;
  margin-right: 1.6rem;
  background-color: #387cb8;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  align-self: flex-end;
  /* Aligns button to the right */
}

.change-password-container .form-group {
  width: 100%;
}

.change-password-form button:hover {
  background-color: #a74629;
}

.change-password-form p {
  color: red;
  margin-top: 10px;
}

/* Specific styles for the settings-content container */
.settings-content {
  display: flex;
  flex-direction: row;
  max-width: 900px;
  margin: 20px auto;
  padding: 25px;
  border: 1px solid #e0e0e0;
  border-radius: 30px;
  background-color: #ffffffb8;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.pop-success-message {
  top: 50%;
  left: 50%;
  position: absolute;
  background-color: #333333f1;
  z-index: 1000;
  color: white;
  padding: 15px;
  border-radius: 5px;
}

.header-dash h1 {
  font-size: 1.6em;
  margin-left: 30px;
}

.header-dash {
  display: flex;
  flex-direction: column;
  text-align: left;
  color: #387cb8;
  max-width: 1400px;
  width: 100%;
  margin: auto;
  margin-top: 20px;
}

.dash-logo {
  height: 50px;
  gap: 0;
  align-self: flex-end;
  margin-top: 20px;
  margin-right: 30px;
}

/* Dashboard Card */
.dashboard-card {
  position: relative;
  display: flex;
  height: 100dvh;
  /* margin-top: 100px; */
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  overflow: hidden;
  /* background-color: transparent;
    border-radius: 20px;
    border: 3px solid #90411c1d;
    border-right: none;
    overflow: hidden; */
}

/* Profile section styles */
.profile-section {
  padding: 20px 40px;
  display: flex;
  align-items: center;
}

/* Profile image */
.profile-section img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

/* Profile info */
.profile-info {
  display: flex;
  flex-direction: column;
}

.profile-info h3 {
  margin: 0;
  font-size: 1.2em;
  color: #000;
}

.profile-info p {
  margin: 0;
  font-size: 0.9em;
  color: #000;
}

/* Confirm popup button styles */
.confirm-popup button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
}

.confirm-popup button:hover {
  background: #0056b3;
}

.confirm-popup button:last-of-type {
  background: #ccc;
}

.confirm-popup button:last-of-type:hover {
  background: #999;
}

/* Content styles */
.dashboard-content {
  flex: 1;
  padding: 20px;
  overflow-y: hidden;
  /* Hide scrollbar initially */
  scrollbar-width: none;
  /* Firefox */
  transition: margin-left 0.3s ease;
}

.dashboard-content.expanded {
  margin-left: 0;
}

.db-sections::-webkit-scrollbar {
  width: 0;
  /* Chrome, Safari */
  height: 0;
  /* Chrome, Safari */
}

/* Show scrollbar on hover */

.db-section::-webkit-scrollbar,
.sections::-webkit-scrollbar {
  width: 12px;
  /* Show scrollbar width */
}

.db-section::-webkit-scrollbar-track,
.sections::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 10px;
}

.db-section::-webkit-scrollbar-thumb,
.sections::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

/* Section styles */
.db-section {
