/* AddCourseForm styles */
.add-course-form {
  display: flex;
  flex-direction: column;
}

.add-course-form label {
  margin-bottom: 10px;
  color: #387cb8c7;
}

.add-course-form input,
.add-course-form textarea,
.add-course-form select {
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 20px;
}

.add-course-form button {
  padding: 10px 20px;
  background-color: #387cb8;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-course-form button:hover {
  background-color: #0056b3;
}

/* Hamburger menu */
.hamburger-menu {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1000;
}

.hamburger-menu span {
  width: 100%;
  height: 3px;
  background-color: #333;
  border-radius: 2px;
}

@media (max-width: 768px) {
  .header-dash h1 {
    font-size: 1.2em;
    margin-left: 30px;
  }

  .header-dash {
    display: flex;
    flex-direction: column;
    text-align: left;
    color: #387cb8;
    max-width: 1400px;
    width: 100%;
    margin: auto;
    margin-top: 0px;
  }

  .dash-logo {
    height: 30px;
    gap: 0;
    align-self: flex-end;
    margin-top: 20px;
    margin-right: 30px;
  }

  .dashboard-card {
    width: 95%;
    margin-left: 10px;
    margin-right: 10px;
  }

  .db-section {
    width: 105%;
    margin-left: -18px;
  }

  .dashboard-content {
    margin-left: 0;
    width: 90%;
  }
}

.error-message {
  color: red;
  font-size: 14px;
  margin-top: 10px;
}

.success-message {
  color: green;
  font-size: 14px;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .settings-content {
    flex-direction: column;
  }
}

@media (min-width: 1920px) and (max-width: 2560px) {
  .header-dash {
    max-width: 1900px;
  }

  .header-dash h1 {
    font-size: 32px;
  }

  .dash-logo {
    height: 80px;
  }

  .profile-info {
    font-size: 24px;
  }

  .profile-section img {
    height: 100px;
    width: auto;
  }

  .settings-content {
    max-width: 1900px;
    margin-left: 200px;
  }


}

/* Overlay for the popup */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  /* Semi-transparent background */
  z-index: 999;
}

.su-loader {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
  z-index: 99999;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.msuccess-message {
  position: fixed;
  top: 50%;
  left: 40vw;
  z-index: 1000;
  background-color: #333333b4;
  padding: 10px;
  border-radius: 5px;
  color: #fff;
}